import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Store, Eye, Star, MessageCircle, TrendingUp, Calendar, ArrowUpDown, ArrowUp, ArrowDown } from "lucide-react";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { subDays, startOfDay } from "date-fns";

type TimeRange = "today" | "7days" | "30days" | "all";
type SortBy = "rating" | "views" | "comments" | "favorites";
type SortOrder = "desc" | "asc";
const AdminRestaurantStatsList = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const { localizeRestaurant } = useLocalizedContent();
  const [timeRange, setTimeRange] = useState<TimeRange>("30days");
  const [sortBy, setSortBy] = useState<SortBy>("rating");
  const [sortOrder, setSortOrder] = useState<SortOrder>("desc");

  const getDateFilter = () => {
    const now = new Date();
    switch (timeRange) {
      case "today":
        return startOfDay(now).toISOString();
      case "7days":
        return startOfDay(subDays(now, 6)).toISOString();
      case "30days":
        return startOfDay(subDays(now, 29)).toISOString();
      default:
        return null;
    }
  };

  // Fetch all visible restaurants
  const { data: restaurants, isLoading: restaurantsLoading } = useQuery({
    queryKey: ['admin-restaurants-stats-list'],
    queryFn: async () => {
      const { data, error } = await supabase
        .from('restaurants')
        .select('*')
        .order('rating', { ascending: false });
      if (error) throw error;
      return data;
    },
  });

  const dateFilter = getDateFilter();

  // Fetch all page views count per restaurant
  const { data: viewCounts } = useQuery({
    queryKey: ['admin-all-restaurant-view-counts', timeRange],
    queryFn: async () => {
      let query = supabase.from('page_views').select('target_id, created_at').eq('page_type', 'restaurant');
      if (dateFilter) {
        query = query.gte('created_at', dateFilter);
      }
      const { data, error } = await query;
      if (error) throw error;
      const counts: Record<string, number> = {};
      data.forEach(view => {
        if (view.target_id) {
          counts[view.target_id] = (counts[view.target_id] || 0) + 1;
        }
      });
      return counts;
    },
  });

  // Fetch all comments count per restaurant
  const { data: commentCounts } = useQuery({
    queryKey: ['admin-all-restaurant-comment-counts', timeRange],
    queryFn: async () => {
      let query = supabase.from('comments').select('target_id, created_at').eq('target_type', 'restaurant');
      if (dateFilter) {
        query = query.gte('created_at', dateFilter);
      }
      const { data, error } = await query;
      if (error) throw error;
      const counts: Record<string, number> = {};
      data.forEach(comment => {
        counts[comment.target_id] = (counts[comment.target_id] || 0) + 1;
      });
      return counts;
    },
  });

  // Fetch all favorites count per restaurant
  const { data: favoriteCounts } = useQuery({
    queryKey: ['admin-all-restaurant-favorite-counts', timeRange],
    queryFn: async () => {
      let query = supabase.from('favorites').select('restaurant_id, created_at');
      if (dateFilter) {
        query = query.gte('created_at', dateFilter);
      }
      const { data, error } = await query;
      if (error) throw error;
      const counts: Record<string, number> = {};
      data.forEach(fav => {
        counts[fav.restaurant_id] = (counts[fav.restaurant_id] || 0) + 1;
      });
      return counts;
    },
  });

  // Calculate total stats
  const totalViews = viewCounts ? Object.values(viewCounts).reduce((a, b) => a + b, 0) : 0;
  const totalComments = commentCounts ? Object.values(commentCounts).reduce((a, b) => a + b, 0) : 0;
  const totalFavorites = favoriteCounts ? Object.values(favoriteCounts).reduce((a, b) => a + b, 0) : 0;

  // Sort restaurants based on selected criteria and order
  const sortedRestaurants = restaurants?.slice().sort((a, b) => {
    let diff = 0;
    switch (sortBy) {
      case "views":
        diff = (viewCounts?.[b.id] || 0) - (viewCounts?.[a.id] || 0);
        break;
      case "comments":
        diff = (commentCounts?.[b.id] || 0) - (commentCounts?.[a.id] || 0);
        break;
      case "favorites":
        diff = (favoriteCounts?.[b.id] || 0) - (favoriteCounts?.[a.id] || 0);
        break;
      case "rating":
      default:
        diff = (b.rating || 0) - (a.rating || 0);
    }
    return sortOrder === "desc" ? diff : -diff;
  });

  if (restaurantsLoading) {
    return (
      <div className="space-y-6">
        <Skeleton className="h-8 w-48" />
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          {[1, 2, 3, 4].map(i => <Skeleton key={i} className="h-24" />)}
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {[1, 2, 3, 4, 5, 6].map(i => <Skeleton key={i} className="h-40" />)}
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div className="flex items-center gap-2">
          <TrendingUp className="h-6 w-6 text-primary" />
          <h1 className="text-2xl font-bold">{t('admin.restaurantStatistics', '餐厅统计')}</h1>
        </div>
        <div className="flex items-center gap-4 flex-wrap">
          <div className="flex items-center gap-2">
            <ArrowUpDown className="h-4 w-4 text-muted-foreground" />
            <Select value={sortBy} onValueChange={(v) => setSortBy(v as SortBy)}>
              <SelectTrigger className="w-[140px]">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="rating">{t('admin.sortByRating', '按评分')}</SelectItem>
                <SelectItem value="views">{t('admin.sortByViews', '按浏览量')}</SelectItem>
                <SelectItem value="comments">{t('admin.sortByComments', '按评论数')}</SelectItem>
                <SelectItem value="favorites">{t('admin.sortByFavorites', '按收藏数')}</SelectItem>
              </SelectContent>
            </Select>
            <Button
              variant="outline"
              size="icon"
              onClick={() => setSortOrder(sortOrder === "desc" ? "asc" : "desc")}
              title={sortOrder === "desc" ? t('admin.sortDescending', '从大到小') : t('admin.sortAscending', '从小到大')}
            >
              {sortOrder === "desc" ? <ArrowDown className="h-4 w-4" /> : <ArrowUp className="h-4 w-4" />}
            </Button>
          </div>
          <div className="flex items-center gap-2">
            <Calendar className="h-4 w-4 text-muted-foreground" />
            <Select value={timeRange} onValueChange={(v) => setTimeRange(v as TimeRange)}>
              <SelectTrigger className="w-[140px]">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="today">{t('analytics.today', '今日')}</SelectItem>
                <SelectItem value="7days">{t('analytics.last7Days', '近7天')}</SelectItem>
                <SelectItem value="30days">{t('analytics.last30Days', '近30天')}</SelectItem>
                <SelectItem value="all">{t('analytics.allTime', '所有时间')}</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </div>
      </div>

      {/* Overview Stats */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-primary/10 rounded-lg">
                <Store className="h-5 w-5 text-primary" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.totalRestaurants', '餐厅总数')}</p>
                <p className="text-2xl font-bold">{restaurants?.length || 0}</p>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-blue-500/10 rounded-lg">
                <Eye className="h-5 w-5 text-blue-500" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.totalViews', '总浏览量')}</p>
                <p className="text-2xl font-bold">{totalViews}</p>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-amber-500/10 rounded-lg">
                <MessageCircle className="h-5 w-5 text-amber-500" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.totalComments', '总评论数')}</p>
                <p className="text-2xl font-bold">{totalComments}</p>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-red-500/10 rounded-lg">
                <Star className="h-5 w-5 text-red-500" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.totalFavorites', '总收藏数')}</p>
                <p className="text-2xl font-bold">{totalFavorites}</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Restaurant List */}
      <div>
        <h2 className="text-lg font-semibold mb-4">{t('admin.allRestaurants', '所有餐厅')}</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {sortedRestaurants?.map(restaurant => {
            const localized = localizeRestaurant(restaurant);
            const views = viewCounts?.[restaurant.id] || 0;
            const comments = commentCounts?.[restaurant.id] || 0;
            const favorites = favoriteCounts?.[restaurant.id] || 0;

            return (
              <Card 
                key={restaurant.id} 
                className="cursor-pointer hover:shadow-md transition-shadow"
                onClick={() => navigate(`/admin/restaurant-stats/${restaurant.id}`)}
              >
                <CardHeader className="pb-2">
                  <div className="flex items-center gap-3">
                    {restaurant.image_url ? (
                      <img 
                        src={restaurant.image_url} 
                        alt={localized.name}
                        className="w-12 h-12 rounded-lg object-cover"
                      />
                    ) : (
                      <div className="w-12 h-12 rounded-lg bg-muted flex items-center justify-center">
                        <Store className="h-6 w-6 text-muted-foreground" />
                      </div>
                    )}
                    <div className="flex-1 min-w-0">
                      <CardTitle className="text-base line-clamp-1">{localized.name}</CardTitle>
                      {restaurant.rating > 0 && (
                        <div className="flex items-center gap-1 text-sm text-muted-foreground">
                          <Star className="h-3 w-3 fill-amber-400 text-amber-400" />
                          <span>{restaurant.rating.toFixed(1)}</span>
                        </div>
                      )}
                    </div>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-3 gap-3 text-sm">
                    <div className="flex flex-col items-center p-2 bg-muted/50 rounded-lg">
                      <Eye className="h-4 w-4 text-muted-foreground mb-1" />
                      <span className="font-medium">{views}</span>
                      <span className="text-xs text-muted-foreground">{t('admin.views', '浏览')}</span>
                    </div>
                    <div className="flex flex-col items-center p-2 bg-muted/50 rounded-lg">
                      <MessageCircle className="h-4 w-4 text-muted-foreground mb-1" />
                      <span className="font-medium">{comments}</span>
                      <span className="text-xs text-muted-foreground">{t('admin.comments', '评论')}</span>
                    </div>
                    <div className="flex flex-col items-center p-2 bg-muted/50 rounded-lg">
                      <Star className="h-4 w-4 text-muted-foreground mb-1" />
                      <span className="font-medium">{favorites}</span>
                      <span className="text-xs text-muted-foreground">{t('admin.favorites', '收藏')}</span>
                    </div>
                  </div>
                  <Button variant="outline" size="sm" className="w-full mt-4">
                    {t('admin.viewDetails', '查看详情')}
                  </Button>
                </CardContent>
              </Card>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default AdminRestaurantStatsList;
